@import url(https://fonts.googleapis.com/css2?family=Syne:wght@400;500;600;700;800&display=swap);
* {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
}

body::-webkit-scrollbar {
  display: none;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  font-size: 80%;
}

h1,
h2,
h3,
h4,
h5,
p {
  margin: 3rem 0 1.38rem;
  /* font-family: 'DM Sans', sans-serif;
  font-weight: 400; */
  font-family: 'Syne', sans-serif;
  line-height: 1.3;
}

h1 {
  margin: 0;
  font-size: 4.209rem;
  font-weight: 700;
}

h2 {
  font-size: 3.157rem;
}

h3 {
  font-size: 2.369rem;
}

h4 {
  font-size: 1.777rem;
}

h5 {
  font-size: 1.333rem;
}
.App{cursor:none;background:url(/Cube_Portfolio/static/media/Background.2f634e65.png);position:relative;height:100vh;display:flex;justify-content:center;align-items:center}.App .home__navs{position:absolute;top:0;left:0;display:flex;justify-content:center;align-items:center;grid-gap:1rem;gap:1rem;padding:0.7rem 1rem}.App .home__navs:hover h1{animation:0.7s h1Animate;-webkit-animation:0.7s h1Animate}@keyframes h1Animate{75%{transform:translateX(30%);-webkit-transform:translateX(30%);-moz-transform:translateX(30%);-ms-transform:translateX(30%);-o-transform:translateX(30%)}100%{transform:translateX(0);-webkit-transform:translateX(0);-moz-transform:translateX(0);-ms-transform:translateX(0);-o-transform:translateX(0)}}.App .home__navs:hover .arrows{transform:translateX(50%);-webkit-transform:translateX(50%);-moz-transform:translateX(50%);-ms-transform:translateX(50%);-o-transform:translateX(50%)}.App .home__navs .arrows{font-size:3rem;transition:0.5s cubic-bezier(0.18, 0.89, 0.32, 1.28);-webkit-transition:0.5s cubic-bezier(0.18, 0.89, 0.32, 1.28);-moz-transition:0.5s cubic-bezier(0.18, 0.89, 0.32, 1.28);-ms-transition:0.5s cubic-bezier(0.18, 0.89, 0.32, 1.28);-o-transition:0.5s cubic-bezier(0.18, 0.89, 0.32, 1.28)}.App .contact__icons{margin-bottom:1rem;position:absolute;bottom:0;left:auto;display:flex;justify-content:center;align-items:center;grid-gap:5rem;gap:5rem;padding:0.7rem 1rem}.App .contact__icons svg{font-size:3rem;color:white}.App .nav__about{top:50%;transform:rotateZ(-90deg) translate(0%, -50%);-webkit-transform:rotateZ(-90deg) translate(0%, -50%);-moz-transform:rotateZ(-90deg) translate(0%, -50%);-ms-transform:rotateZ(-90deg) translate(0%, -50%);-o-transform:rotateZ(-90deg) translate(0%, -50%);color:#000}.App .nav__work{left:auto;color:#000}.App .nav__contact{top:50%;left:auto;right:0;transform:rotateZ(90deg) translateY(-50%);-webkit-transform:rotateZ(90deg) translateY(-50%);-moz-transform:rotateZ(90deg) translateY(-50%);-ms-transform:rotateZ(90deg) translateY(-50%);-o-transform:rotateZ(90deg) translateY(-50%);color:#000}.App .container{margin:auto;width:30rem;height:30rem;position:relative;perspective:1000px;color:#176aab;font-weight:bolder}.App .container #cube{width:100%;height:100%;position:absolute;transform-style:preserve-3d;transform:translateZ(-100px);transition:transform 0.5s ease-in-out;-webkit-transform:translateZ(-100px);-moz-transform:translateZ(-100px);-ms-transform:translateZ(-100px);-o-transform:translateZ(-100px);-webkit-transition:transform 0.5s ease-in-out;-moz-transition:transform 0.5s ease-in-out;-ms-transition:transform 0.5s ease-in-out;-o-transition:transform 0.5s ease-in-out}.App .container figure{background-color:#000;margin:0;width:100%;height:100%;display:flex;justify-content:center;align-items:center;position:absolute;padding:2rem 2.5rem}.App .container figure .intro{padding:0.7rem}.App .container figure .front-intro{line-height:1.5rem;font-weight:800}.App .container figure .cube__idle{height:100%;width:100%;padding:1rem;display:flex;justify-content:center;align-items:center}.App .container figure .cube__idle #email,.App .container figure .cube__idle #insta,.App .container figure .cube__idle #linkedin{color:#000;opacity:0;font-size:7rem;transform:rotateZ(-30deg);-webkit-transform:rotateZ(-30deg);-moz-transform:rotateZ(-30deg);-ms-transform:rotateZ(-30deg);-o-transform:rotateZ(-30deg)}.App .container figure .cube__idle #linkedin{transform:rotateZ(30deg);-webkit-transform:rotateZ(30deg);-moz-transform:rotateZ(30deg);-ms-transform:rotateZ(30deg);-o-transform:rotateZ(30deg)}.App .container #cube .front{transform:rotateY(0deg) translateZ(15rem);-webkit-transform:rotateY(0deg) translateZ(15rem);-moz-transform:rotateY(0deg) translateZ(15rem);-ms-transform:rotateY(0deg) translateZ(15rem);-o-transform:rotateY(0deg) translateZ(15rem)}.App .container #cube .back{transform:rotateX(180deg) translateZ(15rem);-webkit-transform:rotateX(180deg) translateZ(15rem);-moz-transform:rotateX(180deg) translateZ(15rem);-ms-transform:rotateX(180deg) translateZ(15rem);-o-transform:rotateX(180deg) translateZ(15rem)}.App .container #cube .right{background:url(/Cube_Portfolio/static/media/cube2.1a89e1a8.png);transform:rotateY(90deg) translateZ(15rem);-webkit-transform:rotateY(90deg) translateZ(15rem);-moz-transform:rotateY(90deg) translateZ(15rem);-ms-transform:rotateY(90deg) translateZ(15rem);-o-transform:rotateY(90deg) translateZ(15rem)}.App .container #cube .left{background:url(/Cube_Portfolio/static/media/cube1.7f6b737b.png);transform:rotateY(-90deg) translateZ(15rem);-webkit-transform:rotateY(-90deg) translateZ(15rem);-moz-transform:rotateY(-90deg) translateZ(15rem);-ms-transform:rotateY(-90deg) translateZ(15rem);-o-transform:rotateY(-90deg) translateZ(15rem)}.App .container #cube .top{transform:rotateX(90deg) translateZ(15rem);-webkit-transform:rotateX(90deg) translateZ(15rem);-moz-transform:rotateX(90deg) translateZ(15rem);-ms-transform:rotateX(90deg) translateZ(15rem);-o-transform:rotateX(90deg) translateZ(15rem)}.App .container #cube .bottom{transform:rotateX(-90deg) translateZ(15rem);-webkit-transform:rotateX(-90deg) translateZ(15rem);-moz-transform:rotateX(-90deg) translateZ(15rem);-ms-transform:rotateX(-90deg) translateZ(15rem);-o-transform:rotateX(-90deg) translateZ(15rem)}.App .cursor{pointer-events:none;z-index:1;position:absolute;transform:translate(-50%, -50%);padding:0.7em;background-color:white;mix-blend-mode:difference;border-radius:50%;transition:0.05s ease-in-out;-webkit-border-radius:50%;-moz-border-radius:50%;-ms-border-radius:50%;-o-border-radius:50%;-webkit-transform:translate(-50%, -50%);-moz-transform:translate(-50%, -50%);-ms-transform:translate(-50%, -50%);-o-transform:translate(-50%, -50%);-webkit-transition:0.05s ease-in-out;-moz-transition:0.05s ease-in-out;-ms-transition:0.05s ease-in-out;-o-transition:0.05s ease-in-out}.App .cursor__hover:hover ~ .cursor{padding:3em;transition:0.2s;-webkit-transition:0.2s;-moz-transition:0.2s;-ms-transition:0.2s;-o-transition:0.2s}

